@import url(https://fonts.googleapis.com/css2?family=Patua+One&display=swap);
body{margin:0;font-family:"Patua One",cursive}.game{max-width:700px;margin:0 auto;padding:30px 0}.game__title{display:block;text-align:center;font-size:35px;margin-bottom:20px}.game__field{display:grid;grid-template-columns:repeat(10, 1fr);grid-template-rows:repeat(3, 70px);grid-gap:15px}.game__card{position:relative;display:flex;align-items:center;justify-content:center;height:100%;width:100%;cursor:pointer;font-size:25px;border-radius:5px;box-shadow:0 2px 8px rgba(154,160,185,.05),0 2px 8px rgba(166,173,201,.2)}.game__opacity{position:absolute;top:0;left:0;height:100%;width:100%;opacity:0;z-index:10;border-radius:5px;box-shadow:0 2px 8px rgba(154,160,185,.05),0 2px 8px rgba(166,173,201,.2);background-color:#fff}.game .disable{opacity:1;transition:opacity .2s linear}.game .enable{opacity:0;transition:opacity .2s linear}
